import { Injectable, Injector } from '@angular/core';
import { IFieldOptions } from '@helix/platform/config/api';
import { IFormBuilderConfig } from '@helix/platform/shared/api';
import { CounterFormControlComponent, ICounterFormControlOptions } from '@helix/platform/shared/components';
import { RX_CONFIG_DESIGNER } from '../config-designer.constant';
import { BaseFieldDefinitionService } from './base-field-definition.service';
import { IConfigFieldDefinitionModel } from '../config-designer.types';
import { IValidationIssue, ValidationIssueType } from '@helix/platform/ui-kit';
import { RX_NUMBER, RxNumberUtilsService } from '@helix/platform/utils';

@Injectable({
  providedIn: 'root'
})
export class SecureFieldDefinitionService extends BaseFieldDefinitionService {
  dataType = RX_CONFIG_DESIGNER.dataTypes.secure.resourceType;

  constructor(injector: Injector, private rxNumberUtilsService: RxNumberUtilsService) {
    super(injector);
  }

  getFieldInspectorConfig(options: IFieldOptions): IFormBuilderConfig {
    const inspectorConfig = super.getFieldInspectorConfig(options);

    // Removed 'Key field for repeated setting' control from Details section
    inspectorConfig[1].controls.pop();

    // Adding controls specific to secure field to Details section
    inspectorConfig[1].controls.splice(
      inspectorConfig[1].controls.length,
      0,
      {
        name: 'minValue',
        component: CounterFormControlComponent,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.config-designer.config-properties.min-length.label'
          ),
          allowIntegerOnly: true,
          minValue: 0,
          maxValue: RX_NUMBER.maxInteger
        } as ICounterFormControlOptions
      },
      {
        name: 'maxValue',
        component: CounterFormControlComponent,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.config-designer.config-properties.max-length.label'
          ),
          allowIntegerOnly: true,
          minValue: 0,
          maxValue: RX_NUMBER.maxInteger
        } as ICounterFormControlOptions
      }
    );

    return inspectorConfig;
  }

  validate(fieldModel: IConfigFieldDefinitionModel): IValidationIssue[] {
    const validationIssues = super.validate(fieldModel);

    if (this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.minValue) && Number(fieldModel.minValue) < 0) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.invalid-min-length-error.message'
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.maxValue) && Number(fieldModel.maxValue) < 0) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.invalid-max-length-error.message'
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.minValue && Number(fieldModel.minValue) > RX_NUMBER.maxInteger) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.config-designer.config-properties.min-length.label'
            ),
            maxValue: RX_NUMBER.maxInteger
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.maxValue && Number(fieldModel.maxValue) > RX_NUMBER.maxInteger) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.config-designer.config-properties.max-length.label'
            ),
            maxValue: RX_NUMBER.maxInteger
          }
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    if (
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.minValue) &&
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.maxValue) &&
      Number(fieldModel.minValue) > Number(fieldModel.maxValue)
    ) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.config-designer.config-properties.min-length.label'
            ),
            maxValue: this.translateService.instant(
              'com.bmc.arsys.rx.client.config-designer.config-properties.max-length.label'
            )
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    return validationIssues;
  }
}
